import React from "react";
import Card from "../Shared/Card/Card";
import CardListItem from "./CardListItem";
import * as tscFormat from "../../util/todaysCardFormat";

export default function CardList({ card, uv, air, sun }) {
  // card -  : vec / : wsd
  // card - : reh
  // card - : rn1
  // card - ,,  : fcstTime
  // sun - : sunrise / : sunset
  // air - : pm10Grade1h / : pm25Grade1h
  // air - : stationName / : dataTime
  // uv - : h0~h24

  const data = tscFormat.compoundCardData(card, uv, air, sun);

  return (
    <div className="flex flex-row flex-wrap justify-evenly content-start">
      {data.map((item, index) => (
        <Card key={index} styles="w-[19.313rem] h-[15rem] p-5 mb-10">
          <CardListItem
            type={item?.type}
            title={item?.title}
            text1={item?.text1}
            value1={item?.value1}
            text2={item?.text2}
            value2={item?.value2}
            color1={item?.color1}
            color2={item?.color2}
            stationName={item?.stationName}
            time={item?.time}
          ></CardListItem>
        </Card>
      ))}
    </div>
  );
}
